import React from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './Header.css'; // Import the CSS file

function Header() {
  const navbarStyle = {
    boxShadow: '0 2px 4px rgba(0,0,0,.1)',
    padding: '1rem 0',
    position: 'relative',
  };

  const navLinkStyle = {
    fontSize: '1.1rem',
    position: 'relative',
    padding: '0.5rem 1rem', // Add padding to increase clickable area
  };

  const colorBarStyle = {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    height: '6px', 
    background: 'linear-gradient(to right, #0960fa 80%, #D5A00F 80%)',
  };

  return (
    <Navbar bg="light" expand="lg" style={navbarStyle}>
      <div style={colorBarStyle}></div>
      <Container>
        <Navbar.Brand as={Link} to="/" style={{ fontSize: '1.8rem' }}>Labo Langage</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto">
            <Nav.Link as={Link} to="/" style={navLinkStyle} className="nav-item">Accueil</Nav.Link>
            <Nav.Link as={Link} to="/research" style={navLinkStyle} className="nav-item">Recherche</Nav.Link>
            <Nav.Link as={Link} to="/publications" style={navLinkStyle} className="nav-item">Publications</Nav.Link>
            <Nav.Link as={Link} to="/team" style={navLinkStyle} className="nav-item">Notre quipe</Nav.Link>
            <Nav.Link as={Link} to="/contact" style={navLinkStyle} className="nav-item">Contact</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Header;